<style>
</style>

<template>
    <Activity background="#f6f6f6">
        <div style="font-size: 20px;font-weight: bold;padding: 16px 16px 0px 16px;">Push Message</div>

        <div style="padding: 16px;">
            <div style="background-color: #ffffff;border-radius: 24px;padding: 16px;">
                <div>
                    <textarea v-model="content" style="border: none;width:100%;height: 150px;" placeholder="text data to device"></textarea>
                </div>
            </div>

            <div style="padding: 16px 0px 0px 16px;display: flex; align-items: center;">
                <div style="font-weight: bold;flex-grow: 1;">My Online Devices</div>
                <div>
                    <img @click="refreshDevices(true)" src="../assets/icon_refresh.svg" style="width: 40px; height: 40px; padding: 10px;cursor: pointer;"/>
                </div>
            </div>
            <div style="background-color: #ffffff;border-radius: 24px;padding: 12px;">
                <div v-for="it in clientArray" style="display: flex;align-items: center;padding: 4px;">
                    <div>
                        <img v-if="it.systemType==='android'" src="../assets/icon_system_android.svg" style="width: 18px; height: 18px;"/>
                        <img v-if="it.systemType==='ios'" src="../assets/icon_system_ios.svg" style="width: 18px; height: 18px;"/>
                        <img v-if="it.systemType==='web'" src="../assets/icon_system_web.svg" style="width: 18px; height: 18px;"/>
                        <img v-if="it.systemType==='tv'" src="../assets/icon_system_tv.svg" style="width: 18px; height: 18px;"/>
                    </div>
                    <div style="padding-right: 12px;flex-grow: 1;padding-left: 12px;">{{it.deviceName}}</div>
                    <div>
                        <button @click="send(it.id)" class="h5-button h5-button-blue" style="font-size: 12px;height: 28px;">Send</button>
                    </div>
                </div>
            </div>
        </div>
    </Activity>
</template>

<script>
    export default {
        name: "msg",

        data ()
        {
            return {
                content: "",
                clientArray: []
            };
        },

        mounted ()
        {
            this.refreshDevices(false);
        },

        methods: {
            refreshDevices (mask)
            {
                plus.http.get({
                    url: Api.push_client_list,
                    cfg: {
                        mask: mask
                    },
                    callback: data =>
                    {
                        this.clientArray = data.clientArray;
                    }
                });
            },

            send (clientId)
            {
                if(!this.content)
                {
                    plus.toast("content is empty");
                    return;
                }

                plus.http.post({
                    url: Api.tincat_app_sendContent,
                    param: {
                        clientId: clientId,
                        content: this.content
                    },
                    callback: data =>
                    {
                    }
                });
            }
        }
    }
</script>