<style>
    .vjs-icon-placeholder, .vjs-control-text, .vjs-modal-dialog-content {
        color: #ffffff !important;
    }
</style>

<template>
    <Activity>
        <div style="display: flex; flex-direction: column;position: absolute; left: 250px;top: 0px; right: 300px; bottom: 0px;">
            <div style="font-size: 20px;font-weight: bold;padding: 16px 0px 0px 16px;">Tincat TV</div>
            <div style="padding: 16px 16px 0px 16px;">Due to chrome http cross-domain security issues, m3u8 may not play properly. It is strongly recommended that get Tincat TV App.</div>
            <div style="padding: 16px 16px 0px 16px;">Search "TincatTV" on Google Play Store to get Tincat TV App.</div>

            <div style="padding: 16px;flex-grow: 1;">
                <div ref="playerWrapper" style="background-color: #444444;height: 100%"></div>
            </div>

            <div style="text-align: center;padding: 32px 0px 48px 0px;">
                <button @click="addLocalM3U()" class="h5-button h5-button-white" style="width: 150px;margin: 0px 8px 0px 8px;">Add Local M3U</button>
                <button @click="clearChannelList()" class="h5-button h5-button-white" style="width: 150px;margin: 0px 8px 0px 8px;">Clear Channel List</button>
            </div>

            <input ref="filechooser" type="file" accept="audio/x-mpegurl" style="display: none;"/>
        </div>

        <div style="position: absolute;top: 0px; right: 0px; bottom: 0px; overflow-y: auto;;width: 300px;border-left: solid 1px #dddddd;display: flex;flex-direction: column;">
            <div style="font-weight: bold;text-align: center;padding: 12px 16px 12px 16px;">Channel List</div>
            <div style="padding: 0px 8px 8px 8px;">
                <input v-model="keyword" type="text" style="height: 32px; background-color: #f6f6f6;border-radius: 40px;width: 100%;padding: 0px 16px 0px 16px;" placeholder="search channel"/>
            </div>
            <div style="flex-grow: 1;overflow-y: auto;">
                <div v-for="it in filterArray">
                    <div @click="playVideo(it.url)" class="h5-singleline h5-selector" style="padding: 10px 16px 10px 16px;font-size: 13px;">{{it.name}}</div>
                </div>
            </div>
        </div>
    </Activity>
</template>

<script>
    export default {
        name: "live",

        data()
        {
            return {
                keyword: "",
                channelArray: [],
                filterArray: []
            };
        },

        mounted()
        {
            plus.storage.get("TVChannelList", null, data =>
            {
                if(data)
                {
                    this.channelArray = JSON.parse(data);
                    this.filter();
                }
            });
        },

        methods: {
            addLocalM3U()
            {
                this.$refs.filechooser.removeEventListener("change", this.filechooserListener);
                this.filechooserListener = (e) =>
                {
                    var file = e.target.files[0];
                    if(file)
                    {
                        this.$refs.filechooser.value = "";

                        var reader = new FileReader();
                        reader.onload = () =>
                        {
                            var channels = this.parseM3U(reader.result);
                            for(var i = 0; i < channels.length; i++)
                            {
                                this.channelArray.push(channels[i]);
                            }

                            // 保存数据　
                            plus.storage.set("TVChannelList", JSON.stringify(this.channelArray));
                            this.filter();
                        };
                        reader.readAsText(file, "UTF-8");
                    }
                };
                this.$refs.filechooser.addEventListener("change", this.filechooserListener);
                this.$refs.filechooser.click();
            },

            parseM3U(m3uContent)
            {
                try
                {
                    var lines = m3uContent.split("\n");

                    // 解析m3u
                    var channelArray = [];
                    var channel = {};
                    for(var i = 0; i < lines.length; i++)
                    {
                        var line = lines[i];
                        if(line.length === 0)
                        {
                            continue;
                        }

                        if(line.indexOf("#EXTINF") === 0)
                        {
                            channel.name = line.split(",")[1];
                        }
                        else
                        {
                            channel.url = line;
                            if(channel.name && channel.url)
                            {
                                channelArray.push(channel);
                            }
                            channel = {};
                        }
                    }
                    return channelArray;
                }
                catch(e)
                {
                    plus.toast("error m3u");
                }
            },

            filter()
            {
                if(!this.keyword)
                {
                    this.filterArray = this.channelArray;
                    return;
                }

                var newArray = [];
                for(var i = 0; i < this.channelArray.length; i++)
                {
                    var it = this.channelArray[i];
                    if(it.name.toLowerCase().indexOf(this.keyword.toLowerCase()) > -1)
                    {
                        newArray.push(it);
                    }
                }
                this.filterArray = newArray;
            },

            playVideo(url)
            {
                console.log("播放:" + url);
                if(document.querySelector("#player"))
                {
                    var player = videojs("player");
                    player.dispose();
                }

                $(this.$refs.playerWrapper).append("<video id=\"player\" class=\"video-js vjs-default-skin\" controls style=\"width: 100%;height:100%;\"/>");
                var player = videojs("player", {}, () =>
                {
                    player.on("error", e =>
                    {
                        plus.toast("play failed");
                    });
                });
                player.src({
                    src: url.replace("\r", "")
                });
                player.play();
            },

            clearChannelList()
            {
                if(confirm("Delete all tv channels?"))
                {
                    plus.storage.set("TVChannelList", "");
                    this.channelArray = [];
                    this.filter();
                }
            }
        },

        watch: {
            keyword()
            {
                this.filter();
            }
        }
    }
</script>