<style>
</style>

<template>
	<Activity>
		<div style="text-align: center;">
			<div style="font-weight: bold;padding: 64px 0px 32px 0px;font-size: 16px;">Signin</div>
			<div id="qrcode" style="display: flex; justify-content: center;height: 170px;"></div>
			<div style="margin-top: 32px;">Scan the QR code with Tincat App</div>
		</div>
	</Activity>
</template>

<script>
export default {
	name: "signin",

	data ()
	{
		return {};
	},

	mounted ()
	{
		// 关闭推送
		window.PushService.stop();

		plus.http.get({
			url: Api.user_token_create,
			param: {
				app: "tincat",
				sessionType: "web"
			},
			callback: data =>
			{
				$("#qrcode").qrcode({width: 170, height: 170, text: "tincat://chrome/signin?token=" + data.token});
				this.checkToken(data.token);
			}
		});
	},

	unmounted ()
	{
		clearInterval(this.timer);
	},

	methods: {
		checkToken (token)
		{
			this.timer = setInterval(() =>
			{
				plus.http.get({
					url: Api.user_token_check + "/" + token,
					cfg: {
						mask: false
					},
					callback: data =>
					{
						if(data.user)
						{
							clearInterval(this.timer);

							// 保存用户信息
							data.user.deviceType = "web";
							plus.user.setUserInfo(data.user, () =>
							{
								// 开启推送
								window.PushService.start();

								// 前往首页
								plus.browser.open("/home");
							});
						}
					}
				});
			}, 3000);
		}
	}
}
</script>