<style>
</style>

<template>
    <router-view v-slot="{ Component }">
        <keep-alive max="32" :include="includeList">
            <component :is="Component"></component>
        </keep-alive>
    </router-view>
</template>

<script>
    export default {
        data()
        {
            return {
                includeList: [],
                clearPage: ["signin", "home", "bookmark", "live", "msg"]
            };
        },

        watch: {
            $route(to, from)
            {
                console.log("从" + from.path + "到" + to.path + ", isOpen=" + plus.browser.isOpen);
                var name = to.name;
                for(var i = 0; i < this.clearPage.length; i++)
                {
                    if(name === this.clearPage[i])
                    {
                        this.includeList = [];
                        break;
                    }
                }

                // 打开
                if(plus.browser.isOpen)
                {
                    setTimeout(() =>
                    {
                        plus.browser.isOpen = false;
                    }, 500);

                    // 保存缓存
                    this.includeList.push(to.name);
                }
                // 返回
                else
                {
                    // 删除缓存
                    this.includeList.pop();
                }

                // 打印缓存日志
                console.log("缓存:\n" + JSON.stringify(this.includeList, null, 3));
            }
        }
    }
</script>