import {createRouter, createWebHashHistory} from "vue-router"
import bookmark from "../views/bookmark.vue";
import home from "../views/home.vue";
import live from "../views/live.vue";
import msg from "../views/msg.vue";
import signin from "../views/signin.vue";
const routes = [{path: "/bookmark", name:"bookmark", component: bookmark}, {path: "/home", name:"home", component: home}, {path: "/live", name:"live", component: live}, {path: "/msg", name:"msg", component: msg}, {path: "/signin", name:"signin", component: signin}];
const router = createRouter({history: createWebHashHistory(),routes: routes});
export default router