<template>
    <div>
        <slot></slot>
    </div>
</template>

<script>
    export default {
        props: {
            background: {
                type: String,
                default: "#ffffff"
            },
        },

        created: function()
        {
            document.body.style.backgroundColor = this.background;
        },

        activated: function()
        {
            document.body.style.backgroundColor = this.background;
        }
    }
</script>