import {createApp} from "vue"
import router from "./router"
import app from "./app.vue"
import Activity from "./components/Activity.vue"

var profile = "product";
if("product" === profile)
{
	console.log = function()
	{
	};
}

window.Router = router;

// 未登录
plus.http.onUnSigninListener = function()
{
	plus.browser.open("/signin");
};

// 推送服务
window.PushService = {
	socket: null,

	start ()
	{
		var that = this;
		console.log("开始启动推送服务");

		if(this.socket)
		{
			console.log("推送服务正在运行");
			return;
		}

		// 读取用户
		plus.user.getUserInfo(function(user)
		{
			if(!user)
			{
				console.log("用户未登录，推送服务结束启动");
				return;
			}

			var host = "www.netskyx.com";
			var url = "wss://" + host + "/ws/signin/v1?app=tincat&sid=${sid}&systemType=web&deviceName=${deviceName}";
			url = url.replace("${sid}", user.sid);
			url = url.replace("${deviceName}", encodeURIComponent("Chrome " + navigator.platform));

			that.socket = new WebSocket(url);
			that.socket.onopen = function()
			{
				console.log("socket opened");

				// 开始心跳29秒一次
				that.pushClientHeartbeat = setInterval(function()
				{
					if(that.socket)
					{
						that.socket.send("1");
						console.log("发送心跳包");
					}
				}, 1000 * 29);
			};
			that.socket.onmessage = function(e)
			{
				console.log("收到推送消息: " + e.data);
				that._handleMessage(e.data);
			};
			that.socket.onclose = function(e)
			{
				console.log("推送服务停止运行" + e);
				clearInterval(that.pushClientHeartbeat);
				that.pushClientHeartbeat = null;
				that.socket = null;

				// 重连
				setTimeout(function()
				{
					that.start();
				}, 5000);
			};
			that.socket.onerror = function(e)
			{
				console.log(e);
			};

			console.log("推送服务开始运行");
		});
	},

	stop ()
	{
		if(this.socket)
		{
			console.log("推送服务开始停止");
			this.socket.close();
		}
	},

	_handleMessage (data)
	{
		var msg = JSON.parse(data);
		var action = msg.extral.action;
		var actionData = msg.extral.actionData;

		// 推送内容
		if("/push/content" === action)
		{
			// 连接
			if(actionData.indexOf("http://") === 0 || actionData.indexOf("https://") === 0)
			{
				window.open(actionData);
			}
			// 文本
			else
			{
				plus.storage.set("PushMessage", actionData, function()
				{
					window.open("text/index.html");
				});
			}
			return;
		}

		// 推送视频
		if("/push/video" === action)
		{
			window.open("player/index.html?url=" + encodeURIComponent(actionData));
			return;
		}
	}
};

window.signout = () =>
{
	if(confirm("Confirm to signout?"))
	{
		plus.user.setUserInfo(null, () =>
		{
			// 开启推送
			window.PushService.stop();
			// 前往首页
			plus.browser.open("/signin");
		});
	}
};

// 初始化app
var context = createApp(app);
context.config.globalProperties.back = function()
{
	history.back();
};
context.component("Activity", Activity);
context.use(router);
context.mount("#app");

// 用户信息
plus.user.getUserInfo(function(user)
{
	if(user)
	{
		document.querySelector("#name").innerHTML = "Hello " + user.name;
	}
	else
	{
		plus.browser.open("/signin");
	}
});

// 开启推送服务
window.PushService.start();