<style>
</style>

<template>
    <Activity>
        <div style="font-size: 20px;font-weight: bold;padding: 16px 16px 0px 16px;">Bookmark</div>

        <div style="margin-top: 8px;">
            <div @click="openBookmark(it)" v-for="it in bookmarkArray" style="display: flex; align-items: center;height: 40px;padding: 0px 16px; 0px 16px;" class="h5-selector">
                <div>
                    <img v-if="it.url" src="../assets/icon_bookmark.svg" style="width: 16px;height: 16px;"/>
                    <img v-if="!it.url" src="../assets/icon_bookmark_folder.svg" style="width: 16px;height: 16px;"/>
                </div>
                <div style="padding-left: 8px;">{{it.title}}</div>
            </div>
        </div>
    </Activity>
</template>

<script>
    export default {
        name: "bookmark",

        data()
        {
            return {
                bookmarkArray: []
            };
        },

        mounted()
        {
            this.getData();
        },

        methods: {
            getData()
            {
                plus.storage.get("bookmarkArray", null, bookmarkArray =>
                {
                    if(bookmarkArray)
                    {
                        this.bookmarkArray = bookmarkArray;
                    }

                    // 刷新书签
                    plus.http.post({
                        url: Api.tincat_bookmark_snapshot,
                        cfg: {
                            mask: false,
                            cache: true
                        },
                        callback: data =>
                        {
                            this.bookmarkArray = JSON.parse(data.snapshot);
                            plus.storage.set("bookmarkArray", this.bookmarkArray);
                        }
                    });
                });
            },

            openBookmark(bookmark)
            {
                if(bookmark.url)
                {
                    window.open(bookmark.url);
                }
                else
                {
                    var items = [];
                    for(var i = 0; i < bookmark.bookmarkArray.length; i++)
                    {
                        items.push(bookmark.bookmarkArray[i].title);
                    }
                    plus.list(bookmark.title, items, index =>
                    {
                        window.open(bookmark.bookmarkArray[index].url);
                    });
                }
            }
        }
    }
</script>